.footer-section {
    position: relative;
    pointer-events: auto;  
    padding: 10vh 0;
}

.footer-section img{
    width: 0.7rem;
}

.footer-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    margin: 0 auto;
}

.footer-logo img {
    display: flex;
    width: 50%;
}

.footer-middle {
    text-align: left;
}

.footer-address {
    font-family: "NeueMachina_Regular", sans-serif;
    margin-bottom: 10px;
}

.footer-link {
    display: block;
    font-family: "NeueMachina_Regular", sans-serif;
    text-decoration: none;
    margin-bottom: 5px;
    transition: color 0.3s ease;
    text-decoration: underline;
    text-transform: none;
    font-size: 1rem;
}

.footer-menu {
    margin-top: -20px;
}

.footer-menu-link {
    display: block;
    font-family: "NeueMachina_Regular", sans-serif;
    text-decoration: none;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    font-size: 1rem;
    border-bottom: 1px solid black;
    padding: 10px;
    padding-top: 20px;
}

.footer-menu-link:first-child {
    margin-top: 0;
}

.footer-menu-link:hover {
    background-color: black;
    color: white;
}

@media screen and (max-width: 768px) {
    .footer-container {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 5vh;
    }

    .footer-logo img {
        width: 20%;
    }

    .footer-menu {
        margin-top: 0;
    }
}
